/* globals: wc_bookings_admin_calendar_js_params */

/**
 * External dependencies
 */
import { render } from '@wordpress/element';
import DatePickerPopoverMonth from './components/datepicker-popover-month';
import DatePickerPopoverDay from './components/datepicker-popover-day';

const { default_month, default_year, default_day } = wc_bookings_admin_calendar_js_params;

const popoverContainerMonth = document.getElementById( 'wc-bookings-datepicker-container-month' );

if ( null !== popoverContainerMonth ) {
	const pickerMonth = <DatePickerPopoverMonth year={ default_year } month={ default_month } />;
	render( pickerMonth, popoverContainerMonth );
}

const popoverContainerDay = document.getElementById( 'wc-bookings-datepicker-container-day' );

if ( null !== popoverContainerDay ) {
	const pickerDay = <DatePickerPopoverDay year={ default_year } month={ default_month } day={ default_day } />;
	render( pickerDay, popoverContainerDay );
}
